.app {
    height: 100%;
    color: var(--primary-color);
}

.gif {
    max-width: 100%;
    display: block;
    margin: 0 auto;
}

.buttons {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    max-width: 500px;
    margin: 10px auto;
}

.button {
    background-color: var(--secondary-color);
    border: 0;
    color: black;
    margin: 10px;
    padding: 10px 20px;
    text-align: center;
    border-radius: 10px;
    cursor: pointer;
}

.download {
    background-color: var(--primary-color);
    color: white;
}

.buttons > .button {
    flex: 40%;
}


.buttons > .button.download {
    flex: 100%;
}

.loading {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
}

.loading > img {
    width: 200px;
}

.title {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    padding: 10px 0;
    max-width: 350px;
    color: var(--secondary-color);
}

.title > img {
    height: 240px;
    width: 325px;
    object-position: -6001px -1px;
    object-fit: none;
    margin-bottom: 10px;
}

.title > div {
    margin-left: auto;
    font-size: .75em;
}

.modal {
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    z-index: 2;
    background-color: rgba(80, 30, 11, 0.5);
    padding: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.modal.hidden {
    display: none;
}

.modal-container {
    background-color: white;
    border-radius: 10px;
    padding: 10px;
    display: inline-flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.modal-title {
    font-size: 1.5em;
    padding: 10px
}